.switcher {
  width: 40px;
  height: 24px;
  background: linear-gradient(180deg, var(--extra-bg-color) 0%, var(--light-text-color-60) 100%);
  cursor: pointer;
  border-radius: 12px;
  margin-right: 40px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switcher.active {
  background: linear-gradient(180deg, #4f2ee8 0%, var(--sub-text-color) 100%);
}

.switcher-handle {
  width: 20px;
  height: 20px;
  background: -20px 0 no-repeat;
  background-color: var(--light-text-color);
  border-radius: 50%;
  transition: left .3s ease-in-out;
  position: absolute;
  top: 2px;
  left: 1px;
}

.switcher-circle {
  width: 18px;
  height: 18px;
  fill: var(--light-text-color);
  position: absolute;
  top: 3px;
  left: 1px;
}

.switcher.active .switcher-handle {
  background-position: 0 0;
  left: calc(100% - 22px);
}

